<template>
  <div class="row">
    <button class="btn btn-primary" @click="getSummaryInfo">View results</button>
    <div class="col-md-12">
      <div class="card">
        <client-table :data="rows" :columns="columns" :options="options"></client-table>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue/dist/vue.esm'
  import PaperTable from 'components/UIComponents/PaperTable.vue'
  import { AXIOS } from 'src/http-common'

  export default {
    components: {
      PaperTable
    },
    data () {
      return {
        columns: ['id', 'fileName', 'channelIndex', 'strandIndexInChannel', 'eventsNo', 'startTime', 'duration', 'hasTemplate', 'eventsNoTemplate', 'hasComplement', 'eventsNoComplement', 'is2d'],
        rows: this.getSummaryInfo(),
        options: {
          filterByColumn: true,
          perPage: 2,
          texts: {
            filter: 'Filtrar:',
            filterBy: 'Filtrar por {column}',
            count: ' '
          },
          dateColumns: ['birth'],
          dateFormat: 'YYYY-MM-DD',
          datepickerOptions: {
            showDropdowns: true,
            autoUpdateInput: true
          },
          pagination: {chunk: 10, dropdown: false},
          filterable: ['name', 'birth', 'year'],
          headings: {
            name: 'Student Name',
            year: 'Year in School',
            number: 'Number ID',
            id: 'ID'
          }
        }
      }
    },
    methods: {
      getSummaryInfo () {
        AXIOS.get(`api/r/info/0`, {
          params: {
            id: 0
          }
        }).then(response => {
          this.tableData = response.data
        }).catch(e => {
          console.error(e)
        })
      }
    }
  }
</script>
<style>
  .VueTables__date-filter {
    border: 1px solid #ccc;
    padding: 6px;
    border-radius: 4px;
    cursor: pointer;
  }
</style>



